___CSS_LOADER_EXPORT___.push([module.id, "tr.warning[data-v-ede947f8]{background-color:#fff3cd}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Volt/Index.vue"],"names":[],"mappings":"AAgMA,4BACE,wBACF","sourcesContent":["<template>\n  <Layout sidebar=\"volts.index\">\n    <Head title=\"Form Index\"/>\n\n    <PageInfo\n      inertiaBack=\"/page1\"\n      pageIcon=\"fa fa-keyboard-o\"\n      pageTitle=\"Exemplo de um CRUD\"\n    >\n      <nav aria-label=\"breadcrumb\">\n        <ol class=\"breadcrumb\">\n          <li class=\"breadcrumb-item active\">\n            Volts\n          </li>\n        </ol>\n      </nav>\n    </PageInfo>\n\n    <div class=\"container-fluid pt-2\">\n\n      <div class=\"box box-primary\">\n\n        <div class=\"box-header with-border d-flex justify-content-between align-items-center\">\n          <h3>Listagem de Volts</h3>\n\n          <Link :href=\"route('volts.create')\" as=\"button\" class=\"btn btn-primary btn-lg d-flex align-items-center gap-1\" type=\"button\">\n            <i class=\"fa fa-plus-circle fa-2x\"></i>\n            <span>Novo</span>\n          </Link>\n\n        </div>\n\n        <div class=\"box-header with-border\">\n          <div :class=\"{ 'alert-primary': filters.isDirty }\" class=\"d-flex gap-1 alert m-0\">\n            <h3>\n              Filtros:\n            </h3>\n\n            <div>\n              <input v-model=\"filters.nameTerm\" class=\"form-control\" placeholder=\"Buscar por nome...\">\n            </div>\n\n            <div>\n              <select v-model=\"filters.status\" class=\"form-select\" style=\"color: black; padding-right: 2rem\">\n                <option value=\"\">Todos os status</option>\n                <option value=\"1\">Somente ativos</option>\n                <option value=\"0\">Somente inativos</option>\n              </select>\n            </div>\n\n            <div>\n              <button class=\"btn btn-default\" @click=\"filters.reset()\">\n                <i class=\"fa fa-times\"></i>\n              </button>\n            </div>\n          </div>\n        </div>\n\n        <div class=\"box-body\">\n          <table class=\"table table-hover\">\n            <thead>\n            <tr>\n              <th class=\"col-6\" scope=\"col\">\n                Nome\n\n                <i v-if=\"props.orderBy != 'nameasc'\" class=\"fa fa-sort-alpha-asc\" @click=\"orderBy('name', 'asc')\"></i>\n                <i v-if=\"props.orderBy != 'namedesc'\" class=\"fa fa-sort-alpha-desc\" @click=\"orderBy('name', 'desc')\"></i>\n\n                <!--\n                                <order-by\n                                  :class=\"{active: orderByIs('name', 'asc')}\"\n                                  column=\"name\"\n                                  direction=\"asc\"\n                                  icon=\"fa fa-sort-alpha-asc\"\n                                  title=\"crescente\"\n                                  @updateOrderBy=\"updateOrderBy\">\n                                </order-by>\n\n                                <order-by\n                                  :class=\"{active: orderByIs('name', 'desc')}\"\n                                  column=\"name\"\n                                  direction=\"desc\"\n                                  icon=\"fa fa-sort-alpha-desc\"\n                                  title=\"desrescente\"\n                                  @updateOrderBy=\"updateOrderBy\">\n                                </order-by>\n                -->\n              </th>\n\n              <th class=\"col-2\" scope=\"col\">Proprietário</th>\n              <th class=\"col-2\" scope=\"col\">Status</th>\n              <th class=\"col-2\" scope=\"col\">\n                Atualização\n                <!--                <span :style=\"(dataFilters.orderBy == 'updated_at' && dataFilters.orderDirection == 'asc') ? 'background: lightgrey' : ''\" style=\"cursor: pointer\" title=\"antigo - recente\">-->\n                <!--                                <i class=\"fa fa-arrow-down\" @click=\"dataFilters.orderBy = 'updated_at'; dataFilters.orderDirection = 'asc'\"></i>-->\n                <!--                            </span>-->\n                <!--                <span :style=\"(dataFilters.orderBy == 'updated_at' && dataFilters.orderDirection == 'desc') ? 'background: lightgrey' : ''\" style=\"cursor: pointer\" title=\"recente - antigo\">-->\n                <!--                                <i class=\"fa fa-arrow-up\" @click=\"dataFilters.orderBy = 'updated_at'; dataFilters.orderDirection = 'desc'\"></i>-->\n                <!--                            </span>-->\n\n              </th>\n              <th class=\"col-2\" scope=\"col\">Ações</th>\n            </tr>\n            </thead>\n\n            <tbody>\n            <tr v-for=\"volt in volts.data\" :key=\"volt.id\" :class=\"{warning: lastSeen == volt.id}\">\n              <td>\n                <div class=\"d-flex gap-1 justify-content-start align-items-center\">\n                  <a style=\"cursor: pointer!important;\" @click=\"toggleImportante(volt)\">\n                    <i v-if=\"volt.importante\" class=\"fa fa-star\"></i>\n                    <i v-else class=\"fa fa-star-o\"></i>\n                  </a>\n                  {{ volt.name }}\n                </div>\n              </td>\n              <td>{{ volt.proprietario }}</td>\n              <td class=\"text-center\">\n                <div class=\"form-check form-switch\">\n                  <input :checked=\"volt.status\" class=\"form-check-input\" role=\"switch\" style=\"cursor: pointer!important;\" type=\"checkbox\" @click=\"toggleStatus(volt)\">\n                </div>\n              </td>\n              <td>{{ volt.updated_at }}</td>\n              <td>\n                <Link :href=\"route('volts.edit', volt.id)\">\n                  editar\n                </Link>\n              </td>\n            </tr>\n\n            </tbody>\n          </table>\n        </div>\n      </div>\n\n      <div class=\"d-flex justify-content-center\">\n        <Pagination :links=\"volts.meta\"/>\n      </div>\n    </div>\n  </Layout>\n</template>\n\n<script setup>\nimport Pagination from \"@/Shared/Pagination\";\nimport { useFilter } from \"@/Components/Filter/Composables/useFilter\";\nimport { usePersistScroll } from \"@/Composables/usePersistScroll\";\nimport { Inertia } from \"@inertiajs/inertia\";\n\nconst props = defineProps({\n  volts: Object,\n  filters: Object,\n  lastSeen: Number,\n  orderBy: String\n})\n\nconst filters = useFilter(route('volts.filter'), 'put',\n  {\n    nameTerm: props.filters.nameTerm ?? '',\n    status: props.filters.status ?? '',\n  },\n  {\n    nameTerm: '',\n    status: '',\n  })\n\nfunction toggleStatus(volt) {\n  Inertia.put(\n    route('volts.toggle-status', volt.id),\n    {},\n    { preserveState: true, preserveScroll: true }\n  )\n}\n\nfunction toggleImportante(volt) {\n  Inertia.put(\n    route('volts.toggle-importante', volt.id),\n    {},\n    { preserveState: true, preserveScroll: true }\n  )\n}\n\nusePersistScroll()\n\nfunction orderBy(column, direction) {\n  Inertia.get(\n    route('volts.orderby', [column, direction])\n  )\n}\n\n</script>\n\n<style scoped>\ntr.warning {\n  background-color: #fff3cd;\n}\n</style>"],"sourceRoot":""}]);
